import React from 'react';
import moment from "moment";
const Hero = props => {
  return (

   
  
    <tbody>
      <td><input type="radio" value="option1" checked={false} onChange={e => props.onDelete(e, props.hero)}/></td>
   
      <td    onClick={() => props.onSelect(props.hero)}
      className={props.hero === props.selectedHero ? 'selected' : ''}><div className="name">
          {props.hero.name}
        </div></td>   
  
    </tbody>


   

           
     
   
  
  );
};

export default Hero;



