import React, { Component } from 'react';
import EditHero from './EditHero';
import Hero from './Hero';
import api from '../api';

function logChange(val) {
  console.log('Selected: ', val);
}
class Heroes extends Component {
  constructor() {
    super();

    this.state = {
      heroes: [],
      creatingHero: false,
      searchString: '',
      nextId: ''
    
    };
    this.searchHero = this.searchHero.bind(this);
    this.handleEnableAddMode = this.handleEnableAddMode.bind(this);
    this.handleSave = this.handleSave.bind(this);
    this.handleOnChange = this.handleOnChange.bind(this);
    this.handleDelete = this.handleDelete.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
    this.handleSelect = this.handleSelect.bind(this);
    this.handleSearch = this.handleSearch.bind(this);
    this.sortByIdAsc = this.sortByIdAsc.bind(this);
    this.sortByIdDesc = this.sortByIdDesc.bind(this);
  }


  componentDidMount() {
    api.get().then(json => this.setState({ heroes: json }));
  }

  handleSelect(hero) {
    this.setState({ selectedHero: hero });
  }

  handleDelete(event, hero) {
    event.stopPropagation();

    api.destroy(hero).then(() => {
      let heroes = this.state.heroes;
      heroes = heroes.filter(h => h !== hero);
      this.setState({ heroes: heroes });

      if (this.selectedHero === hero) {
        this.setState({ selectedHero: null });
      }
    });
  }

  handleEnableAddMode() {
    var id;
    if(this.state.heroes.length > 0)
    {
      id = Math.max.apply(Math,this.state.heroes.map(function(o){return o.id;}))
    }
    else
    {
     id = 0;
    }

    this.setState({
      addingHero: true,
      nextId: id,
      selectedHero: { id: id+1, name: '',date: '' }
    });
  }

  handleCancel() {
    this.setState({ addingHero: false, selectedHero: null });
  }

  handleSave() {
    let heroes = this.state.heroes;

    if (this.state.addingHero) {
      api
        .create(this.state.selectedHero)
        .then(result => {
          console.log('Successfully created!');

          heroes.push(this.state.selectedHero);
          this.setState({
            heroes: heroes,
            selectedHero: null,
            addingHero: false
          });
        })
        .catch(err => {
          console.log(err);
        });
    } else {
      api
        .update(this.state.selectedHero)
        .then(() => {
          this.setState({ selectedHero: null });
        })
        .catch(err => {});
    }
  }

  handleOnChange(event) {
    let selectedHero = this.state.selectedHero;
    selectedHero[event.target.name] = event.target.value;
    this.setState({ selectedHero: selectedHero });
  }
  searchHero(e)
  {
    this.setState({searchString:e.target.value});      
  }

  handleSearch()
  {  
let sortbyDate = this.state.heroes.sort((a,b)=> Date.parse(new Date(a.date.split("/").reverse().join("-")))-
Date.parse(new Date(b.date.split("/").reverse().join("-"))));
this.setState({heroes: sortbyDate});
  }
  sortByIdAsc()
  {
let sortById = this.state.heroes.sort(function(a,b){return a.id - b.id});
this.setState({heroes: sortById});



  }
  sortByIdDesc()
  {
    let sortById = this.state.heroes.sort(function(a,b){return b.id - a.id});
    this.setState({heroes: sortById});
  }

 

  render() {
      
    

    var heroesList = this.state.heroes,
    searchString = this.state.searchString.trim().toLowerCase();
    if(searchString.length > 0)
    {
    heroesList = heroesList.filter(function(l){
      return l.name.toLowerCase().match(searchString);
    });
    
    
    }
    return (
      <div>
        <div className="row">
          <button onClick={this.handleEnableAddMode}>Add New Hero</button>
          <button onClick={this.handleSearch}>Sort By Date</button>
          <button onClick={this.sortByIdAsc}>Sort By Id Asc</button>
          <button onClick={this.sortByIdDesc}>Sort By Id Desc</button>
          <input placeholder="Search Rumcajs" value={this.state.searchString} onChange={this.searchHero}/>

        </div>
        <thead className="heroes">
          {heroesList.map(hero => {
            return (
              <Hero
                key={hero.id}
                hero={hero}
                onSelect={this.handleSelect}
                onDelete={this.handleDelete}
                selectedHero={this.state.selectedHero}
              />
            );
          })}
        </thead>
        <div className="editarea">
       
          <EditHero
            addingHero={this.state.addingHero}
            onChange={this.handleOnChange}
            selectedHero={this.state.selectedHero}
            onSave={this.handleSave}
            onCancel={this.handleCancel}
          />
         

        </div>
      </div>
    );
  }
}

export default Heroes;



